@font-face {
    font-family: 'Factor A';
    src:
        url('../../fonts/FactorA/FactorA-Regular.eot'),
        url('../../fonts/FactorA/FactorA-Regular.eot?#iefix') format('embedded-opentype'),
        url('../../fonts/FactorA/FactorA-Regular.woff2') format('woff2'),
        url('../../fonts/FactorA/FactorA-Regular.woff') format('woff'),
        url('../../fonts/FactorA/FactorA-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Factor A';
