.conversation {
    position: relative;
    padding: 17px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;

    
    .avatar__container {
        .avatar {
            width: 60px;
            height: 60px;
            object-fit: cover;
            border-radius: 50%;
        }
    }
    .conversation__info {
        display: flex;
        flex-direction: column;
        gap: 7px;

        .username {
            font-weight: 700;
            font-size: 18px;
        }
        .last__message {
            color: rgba(0, 0, 0, 0.6);
        }
    }
    .last__time {
        position: absolute;
        top: 25px;
        right: 17px;
        color: rgba(0, 0, 0, 0.6);
        font-size: 14px;
    }
}